import { Component, OnInit } from '@angular/core';
import { UserService } from './services/usuario.service';
import { Usuario } from './models/usuario';

import { BreakpointObserver, Breakpoints } from '@angular/cdk/layout';
import { Observable } from 'rxjs';
import { map, shareReplay } from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  providers: [UserService]
})
export class AppComponent implements OnInit{
  public titulo = 'Almacen Veronica Godoy Coltters'; 
  public usuario: Usuario;
  public usuario_registro: Usuario;
  public identity;
  public token;
  public errorMessage;
  public alertRegister;

  isHandset$: Observable<boolean> = this.breakpointObserver.observe(Breakpoints.Handset)
  .pipe(
    map(result => result.matches),
    shareReplay()
  );

  constructor(
    private _userService:UserService,
    private breakpointObserver: BreakpointObserver
  ){
    this.usuario = new Usuario('','','','','','');
    this.usuario_registro = new Usuario('','','','','','');
  }

  ngOnInit(){
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();

    console.log(this.identity);
    console.log(this.token);
  }

  public onSubmit(){
    console.log(this.usuario);

    //Conseguir los datos del usuario identificado
    this._userService.signup(this.usuario).subscribe(
      response => {
        let identity = response.user;
        this.identity = identity;

        if(!this.identity._id){
            alert("El usuario no esta correctamente identificado");
        }else{           
          //crear elemento en el localstorage para tener al usuario en sesion
            localStorage.setItem('identity', JSON.stringify(identity));

          //Conseguir el token para enviarselo a cada peticion http
          this._userService.signup(this.usuario, 'true').subscribe(
            response => {
              let token = response.token;
              this.token = token;
      
              if(this.token.lenght <= 0){
                  alert("El token no se ha generado correctamente");
              }else{           
                //crear elemento en el localstorage para tener token disponible
                localStorage.setItem('token', token);
                this.usuario = new Usuario('','','','','','');
              }
            },
            error => {
              var errorMessage = <any>error;
      
              if(errorMessage != null){
                this.errorMessage = error.error.message;
                console.log(error);
              }
            }
          );
        }
      },
      error => {
        var errorMessage = <any>error;

        if(errorMessage != null){
          this.errorMessage = error.error.message;
          console.log(error);
        }
      }
    );
  }

  logout(){
    localStorage.removeItem('identity');
    localStorage.removeItem('token');
    localStorage.clear();
    this.identity = null;
    this.token = null;
    //this._router.navigate(['/']);
  }

  onSubmitRegister(){
    console.log(this.usuario_registro);

    this._userService.register(this.usuario_registro).subscribe(
      response => {
        let user = response.user;
        this.usuario_registro = user; 

        if(!user._id){
          this.alertRegister = 'Error al registrarse';
        }else{
          this.alertRegister = 'El registro se ha realizado correctamente, identificate con tu Rut: '+this.usuario_registro.rut;
          this.usuario_registro = new Usuario('','','','','',''); 
        }

      },
      error => {
        var errorMessage = <any>error;

        if(errorMessage != null){
          this.alertRegister = error.error.message;
          console.log(error);
        }
      }
    );
  }

}

export class FormFieldPrefixSuffixExample {
  hide = true;
}

