import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { InversionService } from '../../services/inversion.service';
import { Inversion } from '../../models/inversion';

@Component({
  selector: 'app-editar-inversion',
  templateUrl: '../../components/crear-inversion/crear-inversion.component.html',
  styleUrls: ['./editar-inversion.component.css'],
  providers:[UserService, InversionService]
})
export class EditarInversionComponent implements OnInit {
  public titulo: string;
  public inversion_realizada: Inversion;
  public identity;
  public token;
  public url:string;
  public alertMessage;
  public is_edit;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService,
    private _inversionService: InversionService
  ){
    this.titulo = 'Editar Inversion';
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.inversion_realizada = new Inversion(new Date(), 0, '');
    this.is_edit = true;
   }

  ngOnInit() { 
    this.getInversion();
  }

  getInversion(){
    this._route.params.forEach((params: Params) => {
        let id = params['id'];

        this._inversionService.getInversion(this.token, id).subscribe(
            response => {
                if(!response.inversion){
                  this._router.navigate(['/']);
                }else{
                  this.inversion_realizada = response.inversion;
                }
            },
            error =>{
              var errorMessage = <any>error;
              if(errorMessage != null){
                //this.alertMessage = error.error.message;
                console.log(error);
              }
            }
        );

    });
  }

  onSubmit(){
    this._route.params.forEach((params: Params) => {
      let id = params['id'];

      this._inversionService.editarInversion(this.token, id, this.inversion_realizada).subscribe(
        response => {
            this.inversion_realizada = response.inversion;

            if(!response.inversion){
              this.alertMessage = "Error en el servidor";
            }else{
              this.alertMessage = "La inversion se ha actualizado correctamente";
              console.log("inversion actualizada");
              this._router.navigate(['/inversion']);
            }

        },
        error =>{
          var errorMessage = <any>error;
          if(errorMessage != null){
            this.alertMessage = error.error.message;

            console.log(error);
          }
        }
      );
    });

  }

}
