import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { VentaService } from '../../services/venta.service';
import { Venta } from 'src/app/models/venta';
import { ProductService } from '../../services/producto.service';
import { Producto } from 'src/app/models/producto';


@Component({
  selector: 'app-editar-venta',
  templateUrl: './editar-venta.component.html',
  styleUrls: ['./editar-venta.component.css'],
  providers: [UserService, VentaService, ProductService]
})
export class EditarVentaComponent implements OnInit {

  public titulo: string;
  public venta: Venta;
  public productos: Producto[];//array
  public producto: string[] = [];
  public identity;
  public token;
  public url:string; 
  public alertMessage;
  public is_edit;
  public selected;
  public fecha_: Date;
  public id_productos:string[] = [];
  public prodCantidad:number[] = [];
  public prodPrecio: number[] = []; 
  carriers: any;
  


  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService,
    private _ventaService: VentaService,
    private _productService: ProductService
  ){
    this.titulo = 'Detalle de Venta';
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.is_edit = true;
    this.venta = new Venta(new Date(), '', '', 0, 0, [], [], []); 
   }

  ngOnInit() { 
    console.log('Componente de editar-productos cargado!');
    this.getVenta();
    
  }

  getVenta(){
    this._route.params.forEach((params: Params) => {
        let id = params['id'];

        this._ventaService.getVenta(this.token, id).subscribe(
            response => {
              if(!response.venta){
                this._router.navigate(['/']);
              }else{
                this.venta = response.venta;
                this.selected = this.venta.estado;
                this.fecha_ = this.venta.fecha;
                this.id_productos = this.venta.productos;
                this.prodCantidad = this.venta.prodCantidad;
                this.prodPrecio = this.venta.prodPrecio;
                console.log(this.prodCantidad);
                this.getProductos();
              }
            },
            error =>{
              var errorMessage = <any>error;
              if(errorMessage != null){
                console.log(error);
              }
            }
        );

    });
  }


  getProductos(){

        for(let i=0 ; i<this.id_productos.length ; i++){
          let id = this.id_productos[i];
          this._productService.getProducto(this.token, id).subscribe(
            response => {
                if(!response.producto){
                  this._router.navigate(['/']); 
                }else{
                  this.producto[i] = response.producto.nombre;
                }
            },
            error =>{
              var errorMessage = <any>error;
              if(errorMessage != null){
                console.log(error);
              }
            }
          );
        }
  }

}
