import { ModuleWithProviders } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

//Import Globales
import { InicioComponent } from './components/inicio/inicio.component';
import { BuscarComponent } from './components/buscar/buscar.component';
import { BuscarProductosComponent } from './components/buscar-productos/buscar-productos.component';
import { BuscarVentasComponent } from './components/buscar-ventas/buscar-ventas.component';
import { BuscarReportesComponent } from './components/buscar-reportes/buscar-reportes.component';

//Import Usuario

import { EditarUsuarioComponent } from './components/editar-usuario/editar-usuario.component';

//import Producto
import { ListaProductosComponent } from './components/lista-productos/lista-productos.component';
import { CrearProductoComponent } from './components/crear-producto/crear-producto.component';
import { EditarProductoComponent } from './components/editar-producto/editar-producto.component';

//import Venta
import { ListaVentasComponent } from './components/lista-ventas/lista-ventas.component';
import { CrearVentaComponent } from './components/crear-venta/crear-venta.component';
import { EditarVentaComponent } from './components/editar-venta/editar-venta.component';

//import Reportes
import { ReportesComponent } from './components/reportes/reportes.component';

//import Inversion
import { InversionComponent } from './components/inversion/inversion.component';
import { CrearInversionComponent } from './components/crear-inversion/crear-inversion.component';
import { EditarInversionComponent } from './components/editar-inversion/editar-inversion.component';

const appRoutes: Routes = [

    {path: '', component: InicioComponent},
    {path: 'inicio', component: InicioComponent},
    {path: 'productos', component: ListaProductosComponent},
    {path: 'crear-producto', component: CrearProductoComponent},
    {path: 'editar-producto/:id', component: EditarProductoComponent},
    {path: 'mis-datos', component: EditarUsuarioComponent},
    {path: 'ventas', component: ListaVentasComponent},
    {path: 'crear-venta', component: CrearVentaComponent},
    {path: 'editar-venta/:id', component: EditarVentaComponent},
    {path: 'buscar', component: BuscarComponent},
    {path: 'buscar-productos', component: BuscarProductosComponent},
    {path: 'buscar-ventas', component: BuscarVentasComponent},
    {path: 'buscar-reportes', component: BuscarReportesComponent},
    {path: 'inversion', component: InversionComponent},
    {path: 'reportes', component: ReportesComponent},
    {path: 'crear-inversion', component: CrearInversionComponent},
    {path: 'editar-inversion/:id', component: EditarInversionComponent},
    {path: '**', component: InicioComponent},
    
];

export const appRoutingProviders: any[] = [];
export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes, {useHash: true} );
 