import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { ProductService } from '../../services/producto.service';
import { UploadService } from '../../services/upload.service';
import { Producto } from '../../models/producto';

@Component({
  selector: 'app-editar-producto',
  templateUrl: '../crear-producto/crear-producto.component.html',
  styleUrls: ['./editar-producto.component.css'],
  providers:[UserService, ProductService, UploadService]
})

export class EditarProductoComponent implements OnInit {
  public titulo: string;
  public producto: Producto;
  public identity;
  public token;
  public url:string;
  public alertMessage;
  public is_edit;

  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService,
    private _productService: ProductService,
    private _uploadService: UploadService
  ){
    this.titulo = 'Editar Producto';
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.producto = new Producto('', 0, 0, 0, '');
    this.is_edit = true;
   }

  ngOnInit() {
    console.log('Componente de editar-productos cargado!');
    //llamar  metodo del api para sacar un producto en base a su id getproducto
    this.getProducto();
  }

  getProducto(){
    this._route.params.forEach((params: Params) => {
        let id = params['id'];

        this._productService.getProducto(this.token, id).subscribe(
            response => {
                if(!response.producto){
                  this._router.navigate(['/']);
                }else{
                  this.producto = response.producto;
                }
            },
            error =>{
              var errorMessage = <any>error;
              if(errorMessage != null){
                //this.alertMessage = error.error.message;
                console.log(error);
              }
            }
        );

    });
  }

  onSubmit(){
    console.log(this.producto);
    this._route.params.forEach((params: Params) => {
      let id = params['id'];

      this._productService.editarProducto(this.token, id, this.producto).subscribe(
        response => {
            this.producto = response.producto;

            if(!response.producto){
              this.alertMessage = "Error en el servidor";
            }else{
              this.alertMessage = "El producto se ha actualizado correctamente";
              if(!this.filesToUpload){
                  this._router.navigate(['/productos']);
              }else{
                  //Subir la imagen del artista
                  this._uploadService.makeFileRequest(this.url+'upload-image-producto/'+id, [], this.filesToUpload, this.token, 'image')
                  .then(
                      (result) => {
                          this._router.navigate(['/productos']);
                      },
                      (error) =>{
                          console.log(error);
                      }
                  );
              
              }

            }

        },
        error =>{
          var errorMessage = <any>error;
          if(errorMessage != null){
            this.alertMessage = error.error.message;

            console.log(error);
          }
        }
      );
    });
  }

  public filesToUpload: Array<File>;
  fileChangeEvent(fileInput: any){
    this.filesToUpload = <Array<File>>fileInput.target.files;
  }



}

