import { BrowserModule } from '@angular/platform-browser';
import { LOCALE_ID, NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpClientModule } from '@angular/common/http';
import { routing, appRoutingProviders } from './app.routing';

import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import localeEs from '@angular/common/locales/es';
import { registerLocaleData } from '@angular/common';
registerLocaleData(localeEs);

//Angular Material
import { MatSliderModule } from '@angular/material/slider';
import { MatGridListModule } from '@angular/material/grid-list';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatToolbarModule } from '@angular/material/toolbar';
import { MatButtonModule } from '@angular/material/button';
import { MatTabsModule } from '@angular/material/tabs';
import { LayoutModule } from '@angular/cdk/layout';
import { MatSidenavModule } from '@angular/material/sidenav';
import { MatListModule } from '@angular/material/list';
import { MatDividerModule } from '@angular/material/divider';
import { MatPaginatorModule } from '@angular/material/paginator';
import { MatTableModule } from '@angular/material/table';
import { MatInputModule } from '@angular/material/input';
import { MatSelectModule } from '@angular/material/select';
import { MatSortModule } from '@angular/material/sort';

//Componentes Globales
import { InicioComponent } from './components/inicio/inicio.component';
import { BuscarComponent } from './components/buscar/buscar.component';

//Componentes de Usuario
import { EditarUsuarioComponent } from './components/editar-usuario/editar-usuario.component';

//Componentes de Productos
import { ListaProductosComponent } from './components/lista-productos/lista-productos.component';
import { CrearProductoComponent } from './components/crear-producto/crear-producto.component';
import { EditarProductoComponent } from './components/editar-producto/editar-producto.component';

//Componentes de Ventas
import { ListaVentasComponent } from './components/lista-ventas/lista-ventas.component';
import { CrearVentaComponent } from './components/crear-venta/crear-venta.component';
import { DatosVentaComponent } from './components/datos-venta/datos-venta.component';
import { EditarVentaComponent } from './components/editar-venta/editar-venta.component';

import { BuscarProductosComponent } from './components/buscar-productos/buscar-productos.component';
import { BuscarVentasComponent } from './components/buscar-ventas/buscar-ventas.component';
import { BuscarReportesComponent } from './components/buscar-reportes/buscar-reportes.component';
import { InversionComponent } from './components/inversion/inversion.component';
import { ReportesComponent } from './components/reportes/reportes.component';
import { LoadingComponent } from './components/loading/loading.component';
import { CrearInversionComponent } from './components/crear-inversion/crear-inversion.component';
import { EditarInversionComponent } from './components/editar-inversion/editar-inversion.component';



@NgModule({
  declarations: [
    AppComponent,
    EditarUsuarioComponent,
    InicioComponent,
    ListaProductosComponent,
    CrearProductoComponent,
    ListaVentasComponent,
    CrearVentaComponent,
    EditarProductoComponent,
    DatosVentaComponent,
    EditarVentaComponent,
    BuscarComponent,
    BuscarProductosComponent,
    BuscarVentasComponent,
    BuscarReportesComponent,
    InversionComponent,
    ReportesComponent,
    LoadingComponent,
    CrearInversionComponent,
    EditarInversionComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    ReactiveFormsModule,
    HttpClientModule,
    routing,
    BrowserAnimationsModule,
    MatSliderModule,
    MatGridListModule,
    MatFormFieldModule,
    MatIconModule,
    MatToolbarModule,
    MatButtonModule,
    MatTabsModule,
    LayoutModule,
    MatSidenavModule,
    MatListModule,
    MatDividerModule,
    MatPaginatorModule,
    MatTableModule,
    MatInputModule,
    MatSelectModule,
    MatSortModule
  ],

  providers: [appRoutingProviders, { provide: LOCALE_ID, useValue: 'es' }],
  bootstrap: [AppComponent]
})
export class AppModule { }
