import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { ProductService } from '../../services/producto.service';
import { VentaService } from '../../services/venta.service';
import { Producto } from '../../models/producto';
import { Venta } from '../../models/venta';

@Component({
  selector: 'app-datos-venta',
  templateUrl: './datos-venta.component.html',
  styleUrls: ['./datos-venta.component.css'],
  providers: [UserService, ProductService, VentaService]
})
export class DatosVentaComponent implements  OnChanges, OnInit{  
  selected = 'Vendido';
 
  @Input() id_producto: string;
  @Input() nombre_producto: string;
  @Input() precio_producto: number; 
  @Input() cant_producto: number;
  @Input() cant_vendida: number;

  @Input() nro_producto: number;
  @Input() array: any[] = [];  
  @Input() total_productos: number;
  @Input() total_precio: number;
  @Input() id_prods: any[] = [];
  @Input() cant_prods: any[] = [];
  @Input() precios_prods: any[] = [];

  public producto: Producto;
  public venta: Venta;
  public identity;
  public token;
  public url: string;
  public alertMessage;
  public fecha_: Date;
  public lista_venta: any;
  
  constructor( 
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService,
    private _productService: ProductService,
    private _ventaService: VentaService
  ) { 
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.fecha_ = new Date();
  }

  ngOnChanges(){ 
    this.venta = new Venta(this.fecha_, this.selected, '', this.total_productos, this.total_precio, this.id_prods, this.cant_prods, this.precios_prods);
  }

  ngOnInit() {
    console.log("Componente hijo datos-venta cargado"); 
  }

  refresh(): void {
    this.venta = new Venta(this.fecha_, '', '', 0, 0, [], [], []);
    window.location.reload();
  }

  //Eliminar fila del arreglo de productos
  eliminarFila(index){
      console.log(index);
      this._router.navigate(['/crear-venta']);
  }

  //Guardando la Venta
  onSubmit(){
    if(this.venta.cant_productos == 0){
      this.alertMessage = "No se han ingresado productos!";
      return 0;
    }
    this._ventaService.addVentas(this.token, this.venta).subscribe(
      response => {
        if(!response.venta){
          this.alertMessage = "Error en el servidor";
        }else{
          this.alertMessage = "La Venta se ha realizado correctamente";
          this.venta = response.venta;
          console.log(this.venta); 
          for(let i=0; i<this.venta.productos.length; i++){
           this._productService.getProducto(this.token, this.venta.productos[i]).subscribe( response => {
             const productos_venta: any = response;
             const cantidad: number = this.venta.prodCantidad[i];
             productos_venta.producto.stock = productos_venta.producto.stock - cantidad;
             this._productService.editarProducto(this.token, this.venta.productos[i], productos_venta.producto).subscribe ( response => {
               console.log(response)
             })
             
           })
          }
          this._router.navigate(['/ventas']);
        }
      },
      error =>{
        var errorMessage = <any>error; 
        if(errorMessage != null){
          console.log(error);
        }
      }
    );
  }

}
