import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { VentaService } from '../../services/venta.service';
import { ProductService } from '../../services/producto.service';
import { Producto } from '../../models/producto';
import { Venta } from '../../models/venta';

export interface Transaction {
  nombre: string;
  precio_compra: number; 
  precio_venta: number;
  stock: number;
  image: string
}

@Component({
  selector: 'app-crear-venta',
  templateUrl: './crear-venta.component.html',
  styleUrls: ['./crear-venta.component.css'],
  providers:[UserService, VentaService, ProductService]
})

export class CrearVentaComponent implements OnInit { 
    public titulo: string;
    public venta: Venta;
    public productos: Producto[];
    public producto_venta: object[];
    public foros: any[] = []; //Arreglo de productos con sus respectivos datos
    public prods: any[] = []; //Productos en la venta(no ocupando ahora)
    public identity;
    public token;
    public url:string;
    public alertMessage;
    public nro: number; //cambia su valor cada ves que agrega productos
    public total_productos: number; //Guarda el total precio de los productos
    public total_precio: number; //Guarda el total precio de los productos

    public id_productos: any[] = []; //Guarda el id de productos
    public prodCantidad: any [] = []; //Guarda la cantidad de productos por producto
    public prodPrecio: any[] = []; //Guarda el precio de los productos por producto
    
    public cantidad_ing : Number [] = [1];

    public transactions: Transaction[]; 
    displayedColumns: string[] = ['image', 'nombre', 'precio_venta', 'stock', 'cantidad','opciones']; 
    dataSource: MatTableDataSource<Transaction>;
    @ViewChild(MatSort, {static:false}) sort: MatSort;
    @ViewChild(MatPaginator, {static:false}) paginator: MatPaginator;
  
  constructor(
      private _route: ActivatedRoute, 
      private _router: Router, 
      private _userService: UserService, 
      private _ventaService: VentaService,
      private _productService: ProductService
    ){ 
      this.titulo = 'Aadir Venta';
      this.identity = this._userService.getIdentity();
      this.token = this._userService.getToken();
      this.url = GLOBAL.url;
      this.nro = 0;
      this.total_productos = 0;
      this.total_precio = 0;
  }

  ngOnInit() {
    console.log('Componente de crear-productos cargado!');
    this.getProductos();
  }

  getProductos(){  
    this._route.params.forEach((params: Params) => {

        this._productService.getAllProductos(this.token).subscribe( 
            response => {

              if(!response.productos){
                this._router.navigate(['/']);
              }else{
                this.productos = response.productos;
                this.transactions = this.productos;
                this.dataSource = new MatTableDataSource(this.transactions);
                for(let i=0; i<this.productos.length; i++){
                  this.cantidad_ing[i]=1;
                }
                this.dataSource.sort = this.sort;
                this.dataSource.paginator = this.paginator;
                this.paginator._intl.itemsPerPageLabel = 'Productos por pagina';
              }
            },
            error =>{
              var errorMessage = <any>error;
              if(errorMessage != null){
                //this.alertMessage = error.error.message;
                console.log(error); 
              }
            }
        );
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();  
  }

  public pasarid;
  public pasarnombre;
  public pasarprecio;
  public pasarcantidad;
  public producto_igual;
  public cantidad_vendida;

  anadirProducto(id, nombre, precio, cantidad, cant_vendidad){

    this.pasarid = id; 
    this.pasarnombre = nombre;
    this.pasarprecio = precio;
    this.pasarcantidad = cantidad;
    this.cantidad_vendida = cant_vendidad; 
    this.nro++;
    let precio_total = precio;

    if(this.nro == 1){
      this.foros.push({id, nombre, precio, cantidad, cant_vendidad, precio_total});
      // this.prods.push({id, nombre, cant_vendidad, precio_total});
      this.id_productos.push(id);
      this.prodCantidad.push(cant_vendidad);
      this.prodPrecio.push(precio_total);
      this.TotalProductos();
      this.Total_Precio(precio); 
    }
    else {
      for(let j = 0; j < this.foros.length; j++){
        if(this.pasarid == this.foros[j]["id"]) {
          this.foros[j]["cant_vendidad"] = this.foros[j]["cant_vendidad"] + cant_vendidad;
          this.foros[j]["precio_total"] = this.foros[j]["cant_vendidad"] * precio;
          this.prodCantidad[j] = this.prodCantidad[j] + cant_vendidad;
          this.prodPrecio[j] = this.prodPrecio[j] + precio_total;
          this.TotalProductos();
          this.Total_Precio(precio);
          this.nro=0;
        }
      }

      if(this.nro == 0){
        this.nro++;
      }else{
        this.foros.push({id, nombre, precio, cantidad, cant_vendidad, precio_total});
        this.id_productos.push(id);
        this.prodCantidad.push(cant_vendidad);
        this.prodPrecio.push(precio_total);
        this.TotalProductos();
        this.Total_Precio(precio);
        this.nro++;
      }
    }

  }

  TotalProductos(){
    this.total_productos = this.total_productos + (this.cantidad_vendida*1);
  }

  Total_Precio(precio){
    if(this.total_precio!= null){
      this.total_precio = this.total_precio + precio*this.cantidad_vendida;
    }
  }

}