import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { GLOBAL } from './global';
import { Venta } from '../models/venta';

@Injectable({
  providedIn: 'root'
})
export class VentaService {
    public url: string;

    constructor(private _http: HttpClient){
        this.url = GLOBAL.url;
    }

    //Crear Venta
    addVentas(token, venta: Venta): Observable<any>{
    let params = JSON.stringify(venta);
    let headers = new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': token
    });
      return this._http.post(this.url+'venta', params, {headers: headers});
    } 

    //Mostrar una Venta
    getVenta(token, id:string):Observable<any>{

      let headers = new HttpHeaders({ 'Content-Type': 'application/json',
                                      'Authorization': token
      });
      return this._http.get(this.url+'venta/'+id, {headers:headers}); 
    }

    //Mostrar Ventas
    getVentas(token): Observable<any>{
      let headers = new HttpHeaders({
        'Content-Type': 'application/json',
        'Authorization': token
      });
      return this._http.get(this.url+'ventas', {headers:headers});   
    }

    //Metodo para editar una venta en la BD
    editarVenta(token, id:string, venta: Venta):Observable<any>{
      let params = JSON.stringify(venta);
      let headers = new HttpHeaders({ 'Content-Type': 'application/json',
                                      'Authorization': token
      });

      return this._http.put(this.url+'venta/'+id, params, { headers: headers });
    }

    //Eliminar Venta
    deleteVenta(token, id:string):Observable<any>{
      
      let headers = new HttpHeaders({ 'Content-Type': 'application/json',
                                      'Authorization': token
      });
      return this._http.delete(this.url+'venta/'+id, {headers:headers}); 
    }

    obtenerVentasFecha(token, fecha_ini: string, fecha_ter: string):Observable<any>{

      let headers = new HttpHeaders({ 'Content-Type': 'application/json',
                                      'Authorization': token
      });
      
      return this._http.get(this.url+'venta/'+fecha_ini+'/'+fecha_ter, {headers:headers});  
  }

}
