import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { ProductService } from '../../services/producto.service';
import { Producto } from '../../models/producto';

export interface Transaction {
  nombre: string;
  precio_compra: number; 
  precio_venta: number;
  stock: number;
  image: string
}

@Component({
  selector: 'app-lista-productos',
  templateUrl: './lista-productos.component.html',
  styleUrls: ['./lista-productos.component.css'],
  providers:[UserService, ProductService]
})

export class ListaProductosComponent implements OnInit {

    public titulo: string;
    public productos: Producto[];
    public identity;
    public token;
    public url:string;
    public transactions: Transaction[]; 
    loading: boolean;
    displayedColumns: string[] = ['nombre', 'precio_compra', 'precio_venta', 'stock', 'image', 'opciones'];
    dataSource: MatTableDataSource<Transaction>;
    @ViewChild(MatSort, {static:false}) sort: MatSort;
    @ViewChild(MatPaginator, {static:false}) paginator: MatPaginator;

  constructor(
      private _route: ActivatedRoute,
      private _router: Router,
      private _userService: UserService,
      private _productService: ProductService
  ){
      this.titulo = 'Productos';
      this.identity = this._userService.getIdentity();
      this.token = this._userService.getToken();
      this.url = GLOBAL.url;
      this.loading = true;
   }

   ngOnInit() {
    console.log('Componente de productos cargado!');
    //Conseguir el listado de productos
    this.getProductos();  

  }

  //Metodo para obtener productos de la BD
   getProductos(){  
    this._route.params.forEach((params: Params) => {

        this._productService.getAllProductos(this.token).subscribe( 
            response => {

              if(!response.productos){
                this._router.navigate(['/']);
              }else{
                this.productos = response.productos;
                this.transactions = this.productos;
                this.dataSource = new MatTableDataSource(this.transactions);
                this.dataSource.sort = this.sort;
                this.dataSource.paginator = this.paginator;
                this.paginator._intl.itemsPerPageLabel = 'Productos por pagina';
                this.loading = false;
              }
            },
            error =>{
              var errorMessage = <any>error;
              if(errorMessage != null){
                //this.alertMessage = error.error.message;
                console.log(error);
              }
            }
        );
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
     
  }

  public confirmado;
  onDeleteConfirm(id){ 
    this.confirmado = id;
  }

  onCancelProducto(){
    this.confirmado = null;
  }

  onDeleteProducto(id){
    this._productService.eliminarProducto(this.token, id).subscribe(
        response => {
            if(!response.producto){
              //console.log(response);
              alert('Error en el servidor');
            }else{
              this.getProductos();
            }
        },
        error =>{
          var errorMessage = <any>error;
          if(errorMessage != null){
            //this.alertMessage = error.error.message;
            console.log(error);
          }
        }
    );
  }

}