import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { InversionService } from '../../services/inversion.service';
import { VentaService } from '../../services/venta.service';
import { Venta } from '../../models/venta';
import { Inversion } from '../../models/inversion';
import { from } from 'rxjs';

@Component({
  selector: 'app-reportes',
  templateUrl: './reportes.component.html',
  styleUrls: ['./reportes.component.css'],
  providers: [UserService, InversionService, VentaService]
})
export class ReportesComponent implements OnInit {

  public titulo: string; 
  public ventas: Venta[];
  public inversion: Inversion[];
  public identity;
  public token;
  public url:string;
  public mostrar:boolean;
  public fecha_inicio = new Date().toISOString();
  public fecha_termino = new Date().toISOString();
  public total_inversion: number;
  public total_ventas: number;
  public cant_ventas: number;
  public cant_inversiones: number;
  public ganancias: number;
  loading: boolean;

  constructor(
    private _route: ActivatedRoute, 
    private _router: Router, 
    private _userService: UserService, 
    private _ventaService: VentaService,
    private _inversionService: InversionService
    ) {
    this.titulo = 'Reportes';
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.mostrar = false;
    this.total_inversion = 0;
    this.total_ventas = 0;
    this.cant_inversiones = 0;
    this.cant_ventas = 0;
    this.ganancias = 0;
    this.loading = false;
   }

  ngOnInit() {
  }

  consultar(){

    this.borrarValores();
    this.ejecutar();
    //obteniendo total de inversion
    this._inversionService.obtenerInverionesFecha(this.token, this.fecha_inicio, this.fecha_termino).subscribe(
      response => {
        this.inversion = response.inversiones;
        for(let i=0; i< this.inversion.length; i++){
          this.total_inversion = this.total_inversion + this.inversion[i].inversion
        }
        this.cant_inversiones = this.inversion.length;
      },
      error =>{
        var errorMessage = <any>error;
        if(errorMessage != null){
          console.log(error);
        }
      });
    
      this._ventaService.obtenerVentasFecha(this.token, this.fecha_inicio, this.fecha_termino).subscribe(
        response => {
          this.ventas = response.ventas;
          console.log(this.ventas);
          for(let i=0; i< this.ventas.length; i++){
            this.total_ventas = this.total_ventas + this.ventas[i].precio_total;
          }
          this.cant_ventas = this.ventas.length;
          this.ganancias = this.total_ventas - this.total_inversion;
        },
        error =>{
          var errorMessage = <any>error;
          if(errorMessage != null){
            console.log(error);
          }
        });
        
  }

  ejecutar(){
      this.loading = true;
      setTimeout( () => {
      this.loading = false;
      this.mostrar = true;
    }, 1500);
  }

  borrarValores(){
    this.total_inversion = 0;
    this.total_ventas = 0;
    this.cant_inversiones = 0;
    this.cant_ventas = 0;
    this.ganancias = 0;
  }

}
