import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { ProductService } from '../../services/producto.service';
import { Producto } from '../../models/producto';

@Component({
  selector: 'app-crear-producto',
  templateUrl: './crear-producto.component.html',
  styleUrls: ['./crear-producto.component.css'],
  providers:[UserService, ProductService]
})
export class CrearProductoComponent implements OnInit {
  public titulo: string;
  public producto: Producto;
  public identity;
  public token;
  public url:string; 
  public alertMessage;


  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService,
    private _productService: ProductService
  ){
    this.titulo = 'Aadir Producto';
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.producto = new Producto('', 0, 0, 0, '');
  }

  ngOnInit() {
    console.log('Componente de crear-productos cargado!');
  }

  onSubmit(){
    console.log(this.producto);
    this._productService.crearProducto(this.token, this.producto).subscribe(
      response => {

          if(!response.producto){
            this.alertMessage = "Error en el servidor";
          }else{
            this.alertMessage = "El producto se ha creado correctamente";
            this.producto = response.producto;
            this._router.navigate(['/editar-producto', response.producto._id]);
          }

      },
      error =>{
        var errorMessage = <any>error;
        if(errorMessage != null){
          console.log(error);
        }
      }
    );
  }

}
