import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { InversionService } from '../../services/inversion.service';
import { Inversion } from '../../models/inversion';

export interface Transaction {
  fecha: Date;
  inversion: number; 
  comentarios: string;
}

@Component({
  selector: 'app-inversion',
  templateUrl: './inversion.component.html',
  styleUrls: ['./inversion.component.css'],
  providers:[UserService, InversionService]
})
export class InversionComponent implements OnInit {

  public titulo: string;
  public inversiones: Inversion[];
  public identity;
  public token;
  public url:string;
  public transactions: Transaction[]; 
  loading: boolean;
  public comentario: boolean;
  displayedColumns: string[] = ['fecha', 'inversion', 'comentarios','opciones'];
  dataSource: MatTableDataSource<Transaction>;
  @ViewChild(MatSort, {static:false}) sort: MatSort;
  @ViewChild(MatPaginator, {static:false}) paginator: MatPaginator;


  constructor(
    private _route: ActivatedRoute,
    private _router: Router,
    private _userService: UserService,
    private _inversionService: InversionService
  ){
    this.titulo = "Inversin";
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.loading = true;
    this.comentario = false;
  }

  ngOnInit() {
    console.log('Componente de inversiones cargado!');
    this.getInversiones();  
  }

  getInversiones(){
    this._route.params.forEach((params: Params) => {

      this._inversionService.getInversiones(this.token).subscribe( 
          response => {

            if(!response.inversiones){
              this._router.navigate(['/']);
            }else{
              this.inversiones = response.inversiones;
              this.transactions = this.inversiones;
              this.dataSource = new MatTableDataSource(this.transactions);
              this.dataSource.sort = this.sort;
              this.dataSource.paginator = this.paginator;
              this.paginator._intl.itemsPerPageLabel = 'Inversiones por pagina';
              this.loading = false;
            }
          },
          error =>{
            var errorMessage = <any>error; 
            if(errorMessage != null){
              //this.alertMessage = error.error.message;
              console.log(error);
            }
          }
      );
  });

  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
     
  }

  public confirmado;
  onDeleteConfirm(id){ 
    this.confirmado = id;
  }

  onCancelInversion(){
    this.confirmado = null;
  }

  onDeleteInversion(id){
    this._inversionService.eliminarInversion(this.token, id).subscribe(
        response => {
            if(!response.inversion){
              alert('Error en el servidor');
            }else{
              this.getInversiones();
            }
        },
        error =>{
          var errorMessage = <any>error;
          if(errorMessage != null){
            console.log(error);
          }
        }
    );
  }

  mostrarComentario(){
    this.comentario = true;
  }

} 
