import { Component, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';

import { GLOBAL } from '../../services/global';
import { UserService } from '../../services/usuario.service';
import { VentaService } from '../../services/venta.service';
import { Venta } from '../../models/venta';

import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';

export interface Transaction {
  fecha: Date;
  estado: string;
  nombre_fiador: string;
  cant_productos: number;
  precio_total: number;
  productos: any[]; 
}

@Component({
  selector: 'app-lista-ventas',
  templateUrl: './lista-ventas.component.html',
  styleUrls: ['./lista-ventas.component.css'],
  providers:[UserService, VentaService]
})
export class ListaVentasComponent implements OnInit { 

  public titulo: string;
  public ventas: Venta[]; 
  public identity;
  public token;
  public url:string;
  public transactions: Transaction[]; 
  displayedColumns: string[] = ['fecha', 'estado', 'precio_total', 'opciones'];
  dataSource: MatTableDataSource<Transaction>;
  @ViewChild(MatSort, {static:false}) sort: MatSort;
  @ViewChild(MatPaginator, {static:false}) paginator: MatPaginator;

  loading: boolean;

  constructor(
    private _route: ActivatedRoute, 
    private _router: Router, 
    private _userService: UserService, 
    private _ventaService: VentaService
    ){ 
    this.titulo = 'Ventas';
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.url = GLOBAL.url;
    this.loading = true;
  }

  ngOnInit() {
    
    console.log('Componente de ventas cargado!');
    this.getVentas();
  }

  getVentas(){  
    this._route.params.forEach((params: Params) => { 

        this._ventaService.getVentas(this.token).subscribe( 
            response => {

              if(!response.ventas){
                this._router.navigate(['/']);
              }else{
                this.ventas = response.ventas; 
                this.transactions = this.ventas;
                this.dataSource = new MatTableDataSource(this.transactions);
                this.dataSource.sort = this.sort;
                this.dataSource.paginator = this.paginator;
                this.paginator._intl.itemsPerPageLabel = 'Ventas por pagina';
                this.loading = false;
              }
            },
            error =>{
              var errorMessage = <any>error;
              if(errorMessage != null){
                //this.alertMessage = error.error.message;
                console.log(error);
              }
            }
        );
    });
  }

  public confirmado;
  onDeleteConfirm(id){
    this.confirmado = id;
  }

  onCancelVenta(){
    this.confirmado = null;
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();  
  }

  onDeleteVenta(id){
    this._ventaService.deleteVenta(this.token, id).subscribe(
        response => {
            if(!response.venta){
              alert('Error en el servidor');
            }else{
              this.getVentas();
            }
        },
        error =>{
          var errorMessage = <any>error;
          if(errorMessage != null){
            console.log(error);
          }
        }
    );
  }  

}
