import { Component, OnInit } from '@angular/core';

import { UserService } from '../../services/usuario.service';
import { Usuario } from '../../models/usuario'; 

@Component({
  selector: 'app-editar-usuario',
  templateUrl: './editar-usuario.component.html',
  styleUrls: ['./editar-usuario.component.css']
})
export class EditarUsuarioComponent implements OnInit {
  public titulo: string;
  public usuario: Usuario;
  public identity;
  public token;
  public alertMessage;
  loading: boolean = false;

  constructor(
    private _userService: UserService
  ){ 
    this.titulo = "Actualizar mis datos"; 
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
    this.usuario = this.identity;
  }

  ngOnInit() {
    console.log('Componente editar-usuario cargado');
  }

  onSubmit(){
    this._userService.updateUser(this.usuario).subscribe(
      response => {
          if(!response.user){
              this.alertMessage = 'El usuario no se ha actualizado';
          }else{
              this.ejecutar();
              localStorage.setItem('identity', JSON.stringify(this.usuario));
              document.getElementById("identity_name").innerHTML = this.usuario.nombre + ' ' + this.usuario.apellido;
          }
      },
      error => { 
        var errorMessage = <any>error;

        if(errorMessage != null){
          var body = JSON.parse(error._body);
          this.alertMessage = error.error.message;
          console.log(error);
        }
      }
  );
}

  ejecutar(){
      
    this.loading = true; 
    setTimeout( () => {
      this.loading = false;
      this.alertMessage = 'Datos actualizados correctamente';
    }, 1000);

  }
  

}
