import { Component, OnInit } from '@angular/core';
import { UserService } from '../../services/usuario.service';
import { InversionService } from '../../services/inversion.service'
import { Inversion } from '../../models/inversion'
import { Router } from '@angular/router';

@Component({
  selector: 'app-crear-inversion',
  templateUrl: './crear-inversion.component.html', 
  styleUrls: ['./crear-inversion.component.css'],
  providers: [UserService, InversionService]
})
export class CrearInversionComponent implements OnInit {
  public titulo: string;
  public identity;
  public token;
  public errorMessage;
  public inversion_realizada: Inversion;
  public alertMessage;
  loading: boolean = false;

  constructor(
    private _userService:UserService,
    private _inversionService: InversionService,
    private _router: Router,
    ){
    this.titulo = "Inversin";
    this.inversion_realizada = new Inversion(new Date(), 0, '');
   }

  ngOnInit() {
    this.identity = this._userService.getIdentity();
    this.token = this._userService.getToken();
  }

  onSubmit(){
    
    this._inversionService.crearInversion(this.token, this.inversion_realizada).subscribe(
      response => {
          if(!response.inversion){
            this.alertMessage = "Error en el servidor";
          }else{
            this.ejecutar();
            
          }

      },
      error =>{
        var errorMessage = <any>error;
        if(errorMessage != null){
          console.log(error);
        }
      }
    );

  }
  
  ejecutar(){
    
    this.loading = true; 
    setTimeout( () => {
      this.loading = false;
      this.alertMessage = "La inversin se ha guardado correctamente";
      this.inversion_realizada = new Inversion(new Date(), 0, '');
      //this._router.navigate(['/inversion']);
    }, 1000);

  }

}
